@import url(https://fonts.googleapis.com/css?family=Alata&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:'Alata', sans-serif}body{background:#7895c2}#calculator-main{background:#3d354d;display:grid;grid-template-columns:auto auto auto auto;width:400px;margin:70px auto 0 auto}#calculator-main #history{grid-column:1/5;text-align:right;color:#7895c2;font-size:20px;padding-right:5px;border-bottom:1px solid #000}#calculator-main #display{text-align:right;grid-column:1/5;color:#7895c2;font-size:25px;padding-right:5px}#calculator-main button{border:1px solid #000;color:#7895c2;background:#3d354d;text-align:center;text-decoration:none;font-size:20px;cursor:pointer;padding:25px 0}#calculator-main button::-moz-focus-inner{border:0}#calculator-main button:focus{outline:none}#calculator-main button:hover{color:#c5dcde}#calculator-main #clear{background:#870808;grid-column:1/3}#calculator-main #equals{grid-row:6/8;grid-column:4/5}#calculator-main #zero{grid-row:7/8;grid-column:1/3}

